* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

html {
  transition: all 1s ease;
}

body {
  background-color: #fafafa;
  transition: all 1s ease;
}

main {
  transition: all 1s ease;
}

section {
  transition: all 1s ease;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 25px 40px;
  position: relative;
  box-shadow: 20px 5px 25px 1px #0000000d;
  transition: all 1s ease;
}

nav button {
  background: none;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 500;
  font-size: 1rem;
  cursor: pointer;
  padding: 10px 5px;
  opacity: 0.6;
  transition: all 500ms ease;
}

nav button span {
  padding-right: 10px;
}

nav button:hover {
  opacity: 1;
}

.searchBar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 50px;
}

.searchBar > div {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
}

.search {
  border-radius: 10px;
  padding: 5px 15px;
  transition: all 1s ease;
}

.searchBar img {
  height: 20px;
}

.searchBar input {
  border: none;
  outline: none;
  padding: 5px 5px 5px 10px; /* top right bottom left */
  font-size: 1.1rem;
  border-radius: 10px;
  transition: all 1s ease;
}

.searchBar button {
  background-color: #fff;
  border: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 130px;
  padding: 10px 15px;
  border-radius: 10px;
  cursor: pointer;
  font-size: 1rem;
  box-shadow: 0 1px 2px 0 #0000000d;
  transition: all 1s ease;
}

.countries {
  padding: 0 60px;
  margin: 2rem auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2.5rem;
}

.countries a {
  color: #000;
  text-decoration: none;
}

.country {
  border-radius: 5px;
  box-shadow: 0 10px 5px 0 #0000000d;
  width: 320px;
  cursor: pointer;
  transition: all 1s ease;
}

.country img {
  height: 200px;
  width: 100%;
  border-radius: 5px 5px 0 0;
}

.country h2 {
  padding: 10px 25px;
  font-size: 1.2rem;
}

.country h4 {
  padding-left: 25px;
}

.country h4:last-of-type {
  padding-bottom: 20px;
}

.country h4 span {
  font-weight: 400;
}

.regions {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border-radius: 20px;
}

.regions ul {
  background-color: #fff;
  position: absolute;
  top: 10rem;
  padding: 10px 15px;
  border-radius: 10px;
  font-size: 1rem;
  box-shadow: 0 1px 2px 0 #0000000d;
  width: 130px;
  list-style-type: none;
  cursor: pointer;
  transition: all 1s ease;
}

.hide {
  display: none;
}

.countryInfo {
  background-color: #fafafa;
}

.details {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  padding: 0 45px;
}

.details .main {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  width: 100%;
  margin-bottom: 4rem;
}

.details .main img {
  height: 412px;
  width: 630px;
}

.details #back {
  background-color: #fff;
  color: #000;
  text-decoration: none;
  font-size: 1rem;
  border: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 2rem 0;
  padding: 10px 15px;
  border-radius: spx;
  box-shadow: 0 1px 2px 0 #0000000d;
  cursor: pointer;
  transition: all 1s ease;
}

.details #back span {
  padding-left: 15px;
}

.details .info {
  flex-basis: 40%;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  flex-direction: column;
}

.details .info > article {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  width: 90%;
}

.details .info h1 {
  font-size: 2rem;
}

.details .info ul {
  list-style-type: none;
  margin: 1rem 0;
}

.details .info ul li span {
  font-weight: 600;
}

.details .info a {
  padding: 0 10px;
}

.error {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  min-height: 70vh;
}

.error h1 {
  font-size: 3rem;
  color: #ff0000;
  margin: 2rem 0;
}

.error #back {
  background-color: #ff0000;
  color: #fff;
  font-size: 1.5rem;
  text-decoration: none;
  padding: 15px 30px;
  border-radius: 10px;
  transition: all 1s ease;
}

.error #back:hover {
  background-color: #fff;
  color: #ff0000;
  border: 3px solid #ff0000;
}

.dark {
  background-color: #202c37 !important;
}

.darkSection {
  background-color: #2b3945;
}

.darkText {
  color: #fff;
}

.darkText::placeholder {
  color: #fff;
}

@media screen and (max-width: 1190px) {
  .details {
    padding: 0 30px;
  }

  .details .info > article {
    flex-direction: column;
    align-items: flex-start;
    padding-left: 30px;
  }

  .details .info h1 {
    padding-left: 30px;
  }
}

@media screen and (max-width: 885px) {
  .details .main {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .details .main img {
    margin-bottom: 2rem;
  }

  .details .info {
    width: 100%;
  }

  .details .info > article {
    flex-direction: row;
    padding-left: 0;
  }

  .details .info h1 {
    padding-left: 0;
  }
}

@media screen and (max-width: 760px) {
  .countries {
    padding: 0 30px;
    place-items: center;
  }

  .details .main {
    justify-content: flex-start;
    align-items: flex-start;
  }

  .details .main img {
    width: 100%;
    height: 100%;
  }
}

@media screen and (max-width: 520px) {
  nav {
    padding: 25px 20px;
  }

  nav button span {
    display: none;
  }

  .searchBar {
    flex-direction: column;
    align-items: flex-start;
    padding: 20px 35px;
  }

  .searchBar .search {
    width: 100%;
  }

  .searchBar input {
    width: 100%;
    padding: 5px 5px 5px 15px; /* top right bottom left */
  }

  .searchBar .regions {
    margin: 1rem 0;
  }

  .regions ul {
    top: 13.5rem;
  }

  .countries {
    margin: 0 auto;
  }

  .details .info > article {
    flex-wrap: wrap;
  }
}

@media screen and (max-width: 360px) {
  .countries {
    padding: 0;
  }

  .country {
    width: 250px;
  }

  .country img {
    height: 150px;
  }

  .details .info h1 {
    font-size: 1.5rem;
  }
}
